.inicio-dashboard {
  width: 100%;
  display: grid;
  grid-template-columns: 40% 60%;

  .detalle-dashboard {
    box-shadow: 1px 0px 2px 1px rgb(0 0 0 / 25%);
    // border-radius: 18px;
    position: relative;
    z-index: 995;

    .calendario {
      padding: 20px 20px 0;
      height: 435px;

      a {
        font-size: 12px;
      }

      h2 {
        font-size: 18px;
        text-transform: capitalize;
      }
      .fc-header-toolbar {
        text-align: center;
      }

      .fc-daygrid-event {
        border: none;
        background-color: transparent;
        font-size: 20px;
      }
      .fc .fc-toolbar.fc-header-toolbar {
        margin-bottom: 15px;
      }

      .fc .fc-scrollgrid-liquid {
        height: 275px;

        @media screen and (min-height: 610px) {
          height: 325px;
        }
        @media screen and (min-height: 645px) {
          height: 350px;
        }
        @media screen and (min-height: 670px) {
          height: 350px;
        }
        @media screen and (min-height: 700px) {
          height: 375px;
        }
        @media screen and (min-height: 750px) {
          height: 400px;
        }
      }
      .fc-scroller {
        /* Tamao del scroll */
        &::-webkit-scrollbar {
          width: 8px;
        }

        /* Estilos barra (thumb) de scroll */
        &::-webkit-scrollbar-thumb {
          background: #ccc;
          border-radius: 4px;
        }

        &::-webkit-scrollbar-thumb:active {
          background-color: #999999;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #b3b3b3;
          box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
        }

        /* Estilos track de scroll */
        &::-webkit-scrollbar-track {
          background: #e1e1e1;
          border-radius: 4px;
        }

        &::-webkit-scrollbar-track:hover,
        &::-webkit-scrollbar-track:active {
          background: #d4d4d4;
        }
      }
      .fc .fc-daygrid-day.fc-day-today {
        background-color: #ded6ff !important;
      }
    }

    .recomendaciones {
      padding: 0 20px;
      overflow: auto;
      height: calc(100vh - 460px);
      &::-webkit-scrollbar{
        display: none;
      }

      h3{
        padding: 10px 0;
        position: sticky;
        top: 0;
        z-index: 2;
        background-color: #f7f5f5;
      }
      .recomendacion-content {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .recomendacion__item {
          width: 100%;
          border: 1px solid #c7c7c7;
          border-radius: 1rem;
          padding: 10px 20px;
          gap: 10px;
          display: flex;
          position: relative;
          min-height: 100px;
          &:nth-child(2n) {
            flex-direction: row-reverse;
            .recomendacion-descripcion {
              text-align: right;
            }
            span {
              right: 10px;
              left: initial;
            }
          }
          span {
            position: absolute;
            display: block;
            width: 20px;
            height: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            top: 10px;
            left: 10px;
            background-color: rgb(222 214 255);
            color: #1e088c;
            font-weight: 600;
            font-size: 13px;
          }
          img {
            width: 60px;
            height: 60px;
            align-self: center;
            object-fit: cover;
            object-position: center;
          }
          .recomendacion-descripcion {
            display: flex;
            align-items: center;
            justify-content: center;
  
            h5 {
              margin-top: 10px;
              font-weight: 500;
              color: $color-primario;
            }
            p {
              font-size: 15px;
            }
          }
        }
      }
    }
  }

  .red-social {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    .cabecera {
      width: 100%;
      padding: 16px 24px;
      border-radius: 0 0 20px 20px;
      transition: 0.2s;
      box-shadow: 1px 0px 2px 1px rgb(0 0 0 / 15%);
      height: 340px;
      border: 1px solid #e0e0e0;

      @media screen and (min-width: 1440px) {
        padding: 16px 48px;
      }

      .ultimas-emociones {
        .no-today {
          filter: grayscale(50%);
        }
        .off {
          filter: grayscale(100%);
        }
        .on {
          filter: none;
        }
        .ul {
          position: relative;
          display: flex;
          justify-content: center;
          gap: 10px;
          width: 100%;

          .li {
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;

            span {
              display: grid;
              place-content: center;
              width: 52px;
              user-select: none;
              line-height: 52px;
              font-size: 50px;
              font-family: -webkit-body;
              border-radius: 50%;
            }
          }

          .active {
            border: 2px solid $color-quinto;
            border-radius: 50%;
            cursor: pointer;

            img {
              border: 1px solid transparent;
              border-radius: 50%;
            }
          }
        }
      }
      .search {
        display: grid;
        height: 40px;
        grid-template-columns: calc(80% - 8px) calc(10% - 8px) calc(10% - 8px);
        gap: 8px;
        margin: 16px 0;

        .new-post {
          background-color: $color-tercer;
          color: #fff;
          border-radius: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;

          &:hover {
            background-color: $color-secundario;
          }

          i {
            pointer-events: none;
            font-size: 16px;
            position: relative;
            z-index: 995;
            transform: 0.5s;
          }
        }
      }

      .post {
        opacity: 0;

        form {
          display: flex;
          flex-direction: column;
          align-items: flex-end;

          textarea {
            width: 100%;
            resize: none;
            height: 125px;
            background-color: #eeeff0;
            border-radius: 15px;
            padding: 12px;
            color: #000;
            font-size: 16px;
          }

          .botones {
            width: 100%;
            display: flex;
            justify-content: space-between;

            .audio {
              display: flex;

              button {
                width: 40px;
                margin-right: 8px;

                i {
                  font-size: 18px;
                }
              }
            }

            .submit {
              width: 25%;
            }

            button {
              margin-top: 16px;
              // width: 35%;
              height: 40px;
              cursor: pointer;
              border-radius: 10px;
              background-color: $color-tercer;
              color: #fff;
              display: flex;
              justify-content: center;
              align-items: center;

              &:hover {
                background-color: $color-secundario;
              }
            }
          }
        }
      }
    }

    .cuerpo {
      overflow-y: scroll;
      max-height: 864px;
      transition: 0.2s;
      height: calc(100vh - 184px);
      width: 100%;
      background-color: #f0f2f5;
      border-radius: 15px;
      .post {
        margin: 14px 28px;
        padding: 10px 12px;
        border-radius: 10px;
        background-color: $color-cuarto;
        box-shadow: 1px 1px 1px 0px rgb(0 0 0 / 25%);

        .cabecera-post {
          display: grid;
          grid-template-columns: 1fr 30px;
          justify-content: space-between;

          .header {
            display: flex;
            gap: 0.5rem;
            width: 100%;
            justify-content: start;
            img {
              width: 50px;
              height: 50px;
              object-fit: cover;
              border-radius: 50%;
            }
            h4 {
              margin-right: 12px;
              font-weight: normal;
              font-size: 11px;
              display: flex;
              align-items: center;
            }
          }
          .header-comentario {
            margin-top: 0.5rem;
            img {
              width: 40px;
              height: 40px;
            }
            h3 {
              margin-top: 0 !important;
            }
          }

          h3 {
            color: $color-primario;
            font-size: 16px;
          }

          button {
            background-color: #d41414;
            border-radius: 7px;
            border: none;
            cursor: pointer;
            display: flex;
            height: 30px;
            margin-top: 5px;
            justify-content: center;
            align-items: center;

            &:hover {
              background-color: #b10b0b;
            }

            i {
              pointer-events: none;
              font-size: 14px;
              color: rgb(248, 248, 248);
            }
          }
        }

        p {
          font-size: 16px;
          margin: 16px 0 25px;
        }
        .contenido-c {
          margin: 15px 0;
        }
        p::first-letter {
          text-transform: uppercase;
        }
        .botones {
          width: 100%;
          display: flex;
          justify-content: space-between;

          button {
            cursor: pointer;
            padding: 4px 8px;
            border-radius: 8px;
            background-color: #ded6ff;
            color: $color-secundario;

            &:hover {
              background-color: $color-secundario;
              color: #ded6ff;

              i {
                color: #ded6ff;
              }
            }

            i {
              pointer-events: none;
              color: $color-secundario;
            }
          }
        }

        .comentarios {
          margin-top: 12px;
          display: none;

          .comentario {
            background-color: #ececec;
            padding: 4px 8px;
            border-radius: 12px;
            margin: 4px 0;

            .title-c {
              margin-top: 8px;
              font-size: 14px;
            }
            .contenido-c {
              font-size: 14px;
            }
          }

          .new-comentario {
            display: grid;
            align-items: center;
            grid-template-columns: 1fr 30px;
            gap: 8px;
            margin-top: 8px;

            textarea {
              resize: none;
              border-radius: 8px;
              padding: 8px;
              box-shadow: 1px 1px 1px 0px rgb(0 0 0 / 25%);
              /* Tamao del scroll */
              &::-webkit-scrollbar {
                width: 8px;
              }

              /* Estilos barra (thumb) de scroll */
              &::-webkit-scrollbar-thumb {
                background: #ccc;
                border-radius: 4px;
              }

              &::-webkit-scrollbar-thumb:active {
                background-color: #999999;
              }

              &::-webkit-scrollbar-thumb:hover {
                background: #b3b3b3;
                box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
              }

              /* Estilos track de scroll */
              &::-webkit-scrollbar-track {
                background: #e1e1e1;
                border-radius: 4px;
              }

              &::-webkit-scrollbar-track:hover,
              &::-webkit-scrollbar-track:active {
                background: #d4d4d4;
              }
            }

            .plus-comentario {
              background-color: rgb(0, 143, 0);
              cursor: pointer;
              color: #fff;
              height: 30px;
              border-radius: 8px;

              &:hover {
                background-color: rgb(1, 92, 1);
              }

              i {
                pointer-events: none;
              }
            }
          }
        }
        .comentarios-show {
          display: flex;
          flex-direction: column;
        }
      }
    }
  }
}
